export default {
	data() {
		return {
			providerList: []
		}
	},
	// mounted() { // app端支持
	// 	this.getLoginAuth();
	// },
	// 在H5端中，目前自定义组件中onLoad方法目前还不支持，暂时只能先用vue中的created或者mounted方法。onload目前只在vue文件的页面,但是还可使用onReady(),也可以生效。
	// onLoad() { // app端也无效
	onReady() {
		this.getLoginAuth();
	},
	methods: {
		// 获取当前登录渠道
		getLoginAuth () {
			uni.getProvider({
				service: 'oauth',
				success: (result) => {
					this.providerList = result.provider.map((value) => {
						let providerName = '';
						let icon = '';
						switch (value) {
							case 'weixin':
								providerName = '微信登录'
								icon = 'weixin';
								break;
							case 'qq':
								providerName = 'QQ登录'
								icon = 'QQ';
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录'
								icon = 'xinlangweibo';
								break;
							case 'alipay':
								providerName = '支付宝登录'
								icon = '';
								break;
						}
						return {
							name: providerName,
							id: value,
							icon: icon
						}
					});
					console.log(this.providerList);
				},
				fail: (error) => {
					console.log('获取登录通道失败', error);
				}
			});
		},
		// 登录
		toLogin (provider) {
			uni.login({
				provider: provider.id,



				success: (res) => {
					console.log('login success:', res);
					// 更新保存在 store 中的登录状态
					// this.login(provider.id);
					console.log('登录成功，保存到本地存储，修改当前用户登录状态')
				},
				fail: (err) => {
					console.log('login fail:', err);
				}
			});
		}
	}
}
